export default {
	namespaced: true,
	state: {
		inn: "",
		egrpou: "",
		surname: "",
		name: "",
		patronymic: "",
		companyType: "",
		companyName: "",
		dateOfBirth: "1900-01-01",
		phone: ""
	},
	getters: {
		inn(state) {
			return state.inn;
		},
		egrpou(state) {
			return state.egrpou;
		},
		surname(state) {
			return state.surname;
		},
		name(state) {
			return state.name;
		},
		patronymic(state) {
			return state.patronymic;
		},
		companyType(state) {
			return state.companyType;
		},
		companyName(state) {
			return state.companyName;
		},
		dateOfBirth(state) {
			return state.dateOfBirth;
		},
		phone(state) {
			return "0" + state.phone;
		}
	},
	mutations: {
		changeInnValue(state, value) {
			state.inn = value;
		},
		switchInnValue(state) {
			if (state.inn === null) {
				state.inn = ""
			}
			else {
				state.inn = null
			}
		},
		changeEgrpouValue(state, value) {
			state.egrpou = value;
		},
		changeSurnameValue(state, value) {
			state.surname = value;
		},
		changeNameValue(state, value) {
			state.name = value;
		},
		changePatronymicValue(state, value) {
			state.patronymic = value;
		},
		changeCompanyTypeValue(state, value) {
			state.companyType = value;
		},
		changeCompanyNameValue(state, value) {
			state.companyName = value;
		},
		changeDateOfBirthValue(state, value) {
			state.dateOfBirth = value;
		},
		changePhoneValue(state, value) {
			state.phone = value;
		}
	},
	actions: {
		changeInnValue(store, value) {
			store.commit("changeInnValue", value);
		},
		switchInnValue(store) {
			store.commit("switchInnValue");
		},
		changeEgrpouValue(store, value) {
			store.commit("changeEgrpouValue", value);
		},
		changeSurnameValue(store, value) {
			store.commit("changeSurnameValue", value);
		},
		changeNameValue(store, value) {
			store.commit("changeNameValue", value);
		},
		changePatronymicValue(store, value) {
			store.commit("changePatronymicValue", value);
		},
		changeCompanyTypeValue(store, value) {
			store.commit("changeCompanyTypeValue", value);
		},
		changeCompanyNameValue(store, value) {
			store.commit("changeCompanyNameValue", value);
		},
		changeDateOfBirthValue(store, value) {
			store.commit("changeDateOfBirthValue", value);
		},
		changePhoneValue(store, value) {
			store.commit("changePhoneValue", value);
		}
	}
};



