<template>
  <div>
    <router-view @submit="generateRandomMessages(messagesForLoadingOffers);
                          showCalcForm = false;
                          requestToServ('post', '../Conditions/SendConditions', sendConditionsCallback, 'offersAreLoading', transportData);"
                 :showCalcForm="showCalcForm"

                 :offers="companies"
                 @companySelection="showCheckout($event)"
                 @issue="requestToServ('post', '../Conditions/CreateContracts', sendClientDataCallback, false, clientData);">
    </router-view>

    <div class="loading--container"
         v-if="offersAreLoading">
      <loading :messages="messagesForLoadingOffers"></loading>
    </div>


    <pre style="font-size: 15px;">{{clientData}}</pre>
    <button @click="clearSS()">CLEAR SESSION STORAGE</button>
    <button @click="clearLS()">CLEAR LOCAL STORAGE</button>
  </div>
</template>

<script>
  import {mapGetters} from "vuex";
  import {mapActions} from "vuex";
  import Loading from "./components/Loading";

  export default {
    data() {
      return {
        messagesForLoadingOffers: [
          "Підбираємо найкращі варіанти",
          "Аналізуємо ціни конкурентів",
          "Робимо знижку",
          "Аналізуємо рейтинг страховиків",
          "Аналізуємо результати",
          "Заварюємо каву",
          "Перевіряємо введені дані",
          "Генеруємо список компаній"
        ],
        offersAreLoading: false,
        showCalcForm: true,
        companies: null
      }
    },

    computed: {
      ...mapGetters([
        "transportData",
        "clientData"
      ])
    },

    methods: {
      ...mapActions("selectedConditions", {
        changeSelectedConditions: "changeSelectedConditions"
      }),

      requestToServ(method, url, callback, loadingType, dataForPostRequest) {
        const xmlHttp = new XMLHttpRequest();
        xmlHttp.open(method, url, true);

        if (dataForPostRequest) {
          xmlHttp.send(JSON.stringify(dataForPostRequest));
        }
        else xmlHttp.send();

        if (loadingType) {
          this[loadingType] = true;
        }

        xmlHttp.onreadystatechange = () => {
          console.log(xmlHttp);
          if (xmlHttp.readyState === 4) {
            const answer = JSON.parse(xmlHttp.responseText);

            if (callback) {
              callback(answer);
            }
          }
        }
      },

      sendConditionsCallback(answer) {
        console.log(answer);
        this.companies = answer;
        sessionStorage.setItem("OffersList", "");
        sessionStorage.setItem("OffersList", JSON.stringify(answer));
        this.offersAreLoading = false;
        this.$router.push("/offers");
        this.showCalcForm = true;
      },

      sendClientDataCallback(answer) {
        location.href = answer;

      },

      generateRandomMessages(messages) {
        messages.sort((a, b) => {
          return Math.random() - 0.5
        }); // Перемешиваем массив
      },

      showCheckout(selectedConditions) {
        this.$router.push("/checkout");
        this.changeSelectedConditions(selectedConditions);
      },

      clearLS() {
        localStorage.clear();
      }
      ,

      clearSS() {
        sessionStorage.clear();
      },


      consclientData() {
        console.log(this.clientData);
      }
    },

    components: {
      Loading
    }
  }
</script>

<style lang="less">
  .form {
    &--sub-block {
      margin-bottom: 20px;
    }

    &--radio {
      position: absolute;
      width: 1px;
      height: 1px;
      overflow: hidden;
      clip: rect(0 0 0 0);
      &:checked {
        + .form--label {
          border: 2px solid #0093e8;
          color: #0093e8;

          .form--icon {
            fill: #0093e8;
          }
        }
      }
      &:focus {
        + .form--label {
          box-shadow: 0 0 0 1px #0093e8;
        }
      }
    }

    &--check {
      clip-path: polygon(0 0);
      &:focus ~ .form--checkmark {
        border: 2px solid #0093e8;
      }
      &:checked ~ .form--checkmark {
        background-color: #0093e8;
      }
    }

    &--row {
      display: flex;
      justify-content: center;
      @media only screen and (max-width: 1195px) {
        flex-wrap: wrap;
      }
      @media only screen and (max-width: 525px) {
        justify-content: space-around;
      }
    }

    &--label {
      border-radius: 10px;
      box-sizing: border-box;
      padding: 15px 30px;
      max-width: 215px;
      margin-right: 20px;
      display: flex;
      cursor: pointer;
      transition: all 0.15s;
      border: 2px solid #fefef9;
      &:hover {
        border: 2px solid #0093e8;
        color: #0093e8;
        .form--icon {
          fill: #0093e8;
        }
      }
      @media only screen and (max-width: 1195px) {
        padding: 11px 23px;
        max-width: 175px;
        margin-bottom: 15px;
      }
      @media only screen and (max-width: 477px) {
        max-width: calc(50% - 15px);
        margin: 5px 7.5px;
      }

      &__park {

      }
    }

    &--input {
      min-width: 200px;
      border-radius: 7px;
      padding: 10px 20px;
      border: 2px solid lightgray;
      background-color: #fff;
      font: inherit;
      transition: all 0.15s;
      &:focus {
        border-color: #0093e8;
      }
      &:hover {
        border-color: #0093e8;
      }
      @media only screen and (max-width: 1195px) {
        min-width: 175px;
      }
      @media only screen and (max-width: 700px) {
        min-width: 220px
      }

      &__block {
        display: block;
        margin: 0 auto;
        margin-bottom: 15px;
      }
      &__error {
        border-color: #ff0000cf;
      }
    }

    &--text {
      margin: 10px auto;
      margin-bottom: 30px;
      max-width: 1000px;
      &__bold {
        font-weight: bold;
      }
    }

    &--list {
      list-style: disc;
      text-align: left;
      display: inline-block;
      line-height: 1.5;
      margin-bottom: 5px;
      @media only screen and (max-width: 768px) {
        margin-left: 15px;
      }
    }

    &--label-cont {
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 10px;
      position: relative;
      margin-bottom: 12px;
      cursor: pointer;
      font-size: 22px;
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;
      &:hover .form--checkmark {
        border: 2px solid #0093e8;
      }
      @media only screen and (max-width: 700px) {
        font-size: 18px;
        margin-bottom: 0;
      }
    }

    &--checkmark {
      position: relative;
      display: inline-block;
      margin-left: 5px;
      vertical-align: middle;
      height: 25px;
      width: 25px;
      background-color: lightgray;
      border: 2px solid white;
      transition: all 0.15s;
      &::after {
        content: "";
        position: absolute;
        left: 9px;
        top: 5px;
        width: 5px;
        height: 10px;
        border: solid white;
        border-width: 0 3px 3px 0;
        -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
        transform: rotate(45deg);

      }
    }

    &--icon-wrap {
      display: flex;
      flex-direction: column;
      justify-content: center;
    }

    &--icon {
      width: 33%;
      margin: 0 auto;
      margin-bottom: 10px;
      display: inline-block;
      vertical-align: middle;
      fill: darkgrey;
      transition: all 0.15s;
      &__not {
        width: 38%;
      }
      &__small {
        width: 27%;
      }
      &__small-not {
        width: 33%;
      }
      &__extra-small {
        width: 61%;
      }
      &__extra-small-not {
        width: 28%;
      }
      &__100k {
        width: 33%;
      }
      &__150k {
        width: 45%;
      }
      &__200k {
        width: 57%;
      }
      &__500k {
        width: 69%;
      }
      &__1m {
        width: 81%;
      }
    }

    &--submit {
      margin-top: 10px;
      margin-bottom: 40px;
    }

    &--send {
      font: inherit;
      font-size: 22px;
      color: white;
      border: none;
      border: 2px solid lightgray;
      background-color: #0093e8;
      border-radius: 10px;
      padding: 15px 30px;
      cursor: pointer;
      @media only screen and (max-width: 1650px) {
        font-size: 20px;
        padding: 15px 25px;
      }
      @media only screen and (max-width: 1195px) {
        font-size: 18px;
        padding: 10px 20px;
      }

      &[disabled] {
        background-color: lightgray;
        cursor: not-allowed
      }
    }

    &--error {
      display: block;
      color: #ff0000cf;
      font-weight: bold;
    }
  }

  .label {
    &--wrap {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      align-items: flex-end;
    }
  }

  .router--link {
    display: inline-block;
    color: white;
    background-color: #0093e8;
    text-decoration: none;
    margin-top: 10px;
    font-size: 18px;
    padding: 12px 14px;
    border-radius: 10px;

    &::before {
      content: "";
      display: inline-block;
      width: 13px;
      height: 13px;
      margin-right: 7px;
      background: url(/Content/img/back.svg) center/contain no-repeat;
    }
  }
</style>

<style>
  .v-select {
    display: inline-block;
    padding: 5px !important;
  }

  .v-select .dropdown-toggle {
    border: none !important;
  }

  .v-select input[type=search],
  .v-select input[type=search]:focus {
    font: inherit !important;
  }

  .v-select .dropdown-menu {
    width: auto !important;
    min-width: 100% !important;
  }

  .v-select.open .dropdown-toggle {
    border: none !important;
    border-radius: inherit !important;
  }

  .v-select.single .selected-tag {
    font: inherit !important;
    border: none !important;
  }
</style>

<style>
  .loading--container {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
</style>



