export default {
	namespaced: true,
	state: {
		type: "passport",
		lot: "",
		number: "",
		issuedBy: "",
		when: ""
	},
	getters: {
		type(state) {
			return state.type;
		},
		lot(state) {
			return state.lot;
		},
		number(state) {
			return state.number;
		},
		issuedBy(state) {
			return state.issuedBy;
		},
		when(state) {
			return state.when;
		}
	},
	mutations: {
		changeTypeValue(state, value) {
			state.type = value;
		},
		changeLotValue(state, value) {
			state.lot = value;
		},
		changeNumberValue(state, value) {
			state.number = value;
		},
		changeIssuedByValue(state, value) {
			state.issuedBy = value;
		},
		changeWhenValue(state, value) {
			state.when = value;
		}
	},
	actions: {
		changeTypeValue(store, value) {
			store.commit("changeTypeValue", value);
		},
		changeLotValue(store, value) {
			store.commit("changeLotValue", value);
		},
		changeNumberValue(store, value) {
			store.commit("changeNumberValue", value);
		},
		changeIssuedByValue(store, value) {
			store.commit("changeIssuedByValue", value);
		},
		changeWhenValue(store, value) {
			store.commit("changeWhenValue", value);
		}
	}
};



