export default {
	namespaced: true,
	state: {
		selectedConditions: {
			company: sessionStorage.getItem("selectedCompany") || null,
			frans: sessionStorage.getItem("selectedFrans") || null,
			price: sessionStorage.getItem("selectedPrice") || null,
      coefs: sessionStorage.getItem("selectedCoefs") || null,
      idCompanyMiddleman: sessionStorage.getItem("idCompanyMiddleman") || null
		}
	},
	getters: {
		selectedConditions(state) {
			return state.selectedConditions;
		}
	},
	mutations: {
		changeSelectedConditions(state, value) {
			sessionStorage.setItem("selectedCompany", value.company);
			sessionStorage.setItem("selectedFrans", value.frans);
			sessionStorage.setItem("selectedPrice", value.price);
      sessionStorage.setItem("selectedCoefs", value.coefs);
      sessionStorage.setItem("idCompanyMiddleman", value.idCompanyMiddleman);

			state.selectedConditions = value;
		}
	},
	actions: {
		changeSelectedConditions(store, value) {
			store.commit("changeSelectedConditions", value);
		}
	}
};



