export default {
	namespaced: true,
	state: {
		transType: localStorage.getItem("transportDataTransType") || "Легковий автомобіль",
		transExtraHeader: localStorage.getItem("transportTransExtraHeader") || "Об'єм двигуна",
		transExtraName: localStorage.getItem("transportTransExtraName") || "B 1",
    transExtraTitle: localStorage.getItem("transportTransExtraTitle") || "до 1600 см",

	},
	getters: {
		transType(state) {
			return state.transType;
		},
		transExtraHeader(state) {
			return state.transExtraHeader;
		},
		transExtraName(state) {
			return state.transExtraName;
		},
    transExtraTitle(state) {
      return state.transExtraTitle;
    }
	},
	mutations: {
		changeTransType(state, value) {
			state.transType = value;
			localStorage.setItem("transportDataTransType", value);
		},
		changeTransExtraHeader(state, value) {
			state.transExtraHeader = value;
			localStorage.setItem("transportTransExtraHeader", value);
		},
    changeTransExtraName(state, value) {
			state.transExtraName = value;
			localStorage.setItem("transportTransExtraName", value);
		},
    changeTransExtraTitle(state, value) {
      state.transExtraTitle = value;
      localStorage.setItem("transportTransExtraTitle", value);
    }
	},
	actions: {
		changeTransType(store, value) {
			store.commit("changeTransType", value);
		},
		changeTransExtraHeader(store, value) {
			store.commit("changeTransExtraHeader", value);
		},
		changeTransExtraName(store, value) {
			store.commit("changeTransExtraName", value);
		},
    changeTransExtraTitle(store, value) {
      store.commit("changeTransExtraTitle", value);
    }
	}
};



