import Vue from 'vue';
import VueRouter from 'vue-router';

Vue.use(VueRouter);

const CalcForm = resolve => {
  require.ensure(["./components/CalcForm"], () => {
    resolve(
      require("./components/CalcForm")
    )
  })
};
const CompaniesList = resolve => {
  require.ensure(["./components/CompaniesList"], () => {
    resolve(
      require("./components/CompaniesList")
    )
  })
};
const Checkout = resolve => {
  require.ensure(["./components/Checkout"], () => {
    resolve(
      require("./components/Checkout")
    )
  })
};


const routes = [
  {
    path: '/',
    name: 'Calc',
    component: CalcForm
  },
  {
    path: '/offers',
    name: 'Offers',
    component: CompaniesList
  },
  {
    path: '/checkout',
    name: 'Checkout',
    component: Checkout
  },
  {
    path: '*',
    redirect: '/'
  }
];

export const router = new VueRouter({
  routes,
  mode: 'history'
});



