export default {
	namespaced: true,
	state: {
		region: "",
		city: "",
		street: "",
		streetType: "",
		house: "",
		flat: "",
	},
	getters: {
		region(state) {
			return state.region;
		},
		city(state) {
			return state.city;
		},
		street(state) {
			return state.street;
		},
		streetType(state) {
			return state.streetType;
		},
		house(state) {
			return state.house;
		},
		flat(state) {
			return state.flat;
		}
	},
	mutations: {
		changeRegionValue(state, value) {
			state.region = value;
		},
		changeCityValue(state, value) {
			state.city = value;
		},
		changeStreetValue(state, value) {
			state.street = value;
		},
		changeStreetTypeValue(state, value) {
			state.streetType = value;
		},
		changeHouseValue(state, value) {
			state.house = value;
		},
		changeFlatValue(state, value) {
			state.flat = value;
		}
	},
	actions: {
		changeRegionValue(store, value) {
			store.commit("changeRegionValue", value);
		},
		changeCityValue(store, value) {
			store.commit("changeCityValue", value);
		},
		changeStreetValue(store, value) {
			store.commit("changeStreetValue", value);
		},
		changeStreetTypeValue(store, value) {
			store.commit("changeStreetTypeValue", value);
		},
		changeHouseValue(store, value) {
			store.commit("changeHouseValue", value);
		},
		changeFlatValue(store, value) {
			store.commit("changeFlatValue", value);
		}
	}
};



