export default {
	namespaced: true,
	state: {
		privileges: (localStorage.getItem("transportDataPrivileges") && localStorage.getItem("transportDataPrivileges") !== "false") ?
      localStorage.getItem("transportDataPrivileges")
      :
      false
	},
	getters: {
		privileges(state) {
			return state.privileges;
		},
    privilegesBool(state) {
		  return state.privileges ? true : state.privileges
    }
	},
	mutations: {
		changePrivilegesValue(state, value) {
			if (value === "false") {
				state.privileges = false;
				localStorage.setItem("transportDataPrivileges", false);
			}
			else {
				state.privileges = value;
				localStorage.setItem("transportDataPrivileges", value);
			}
		}
	},
	actions: {
		changePrivilegesValue(store, value) {
			store.commit("changePrivilegesValue", value);
		}
	}
};



