export default {
	namespaced: true,
	state: {
		isLegalEntity: (localStorage.getItem("transportDataIsLegalEntity") === "true" || false )
	},
	getters: {
    isLegalEntity(state) {
			return state.isLegalEntity;
		}
	},
	mutations: {
		changeUserType(state, value) {
			state.isLegalEntity = value === "true";
			localStorage.setItem("transportDataIsLegalEntity", value);
		}
	},
	actions: {
		changeUserType(store, value) {
			store.commit("changeUserType", value);
		}
	}
};



