export default {
	namespaced: true,
	state: {
		otk: {
			state: false,
			term: null
		}
	},
	getters: {
		otk(state) {
			return state.otk.state;
		},
		otkTerm(state) {
			return state.otk.term;
		}
	},
	mutations: {
		changeOtkValue(state, value) {
			state.otk.state = value;
		},
		changeOtkTerm(state, term) {
			state.otk.term = term;
		}
	},
	actions: {
		changeOtkValue(store, value) {
			store.commit("changeOtkValue", value);
		},
		changeOtkTerm(store, term) {
			store.commit("changeOtkTerm", term);
		}
	}
};



