export default {
	namespaced: true,
	state: {
		period: +localStorage.getItem("transportDataPeriod") || 12.0
	},
	getters: {
		period(state) {
			if(state.period === "false") {
				return false
			}
			else {
				return state.period;
			}
		}
	},
	mutations: {
		changePeriod(state, value) {
			state.period = +value || false;
			localStorage.setItem("transportDataPeriod", value);
		}
	},
	actions: {
		changePeriod(store, value) {
			store.commit("changePeriod", value);
		}
	}
};



