export default {
  namespaced: true,
  state: {
    regions: localStorage.getItem("transportDataRegions") || [],
    placeType: localStorage.getItem("transportDataPlaceType") || "address",
    activeRegion: localStorage.getItem("transportDataRegion") || "Київська",
    activeCity: localStorage.getItem("transportDataCity") || "Київ",
    codes: localStorage.getItem("transportDataCodes") || [],
    activeCode: localStorage.getItem("transportDataCodeNum") && localStorage.getItem("transportDataCodeReg") && localStorage.getItem("transportDataCodeCity")
      ?
      {
        codeNum: localStorage.getItem("transportDataCodeNum"),
        regionName: localStorage.getItem("transportDataCodeReg"),
        cityName: localStorage.getItem("transportDataCodeCity")
      }
      : null, // Подтянуть обьект кода МРЕО с LS или сделать activeCode равным null
    foreign: localStorage.getItem("transportDataForeign") || false,
    countries: localStorage.getItem("transportDataCountries") || [],
  },
  getters: {
    regions(state) {
      try {
        return JSON.parse(state.regions);
      } catch (e) {
        return state.regions;
      }
    },
    placeType(state) {
      return state.placeType;
    },
    activeRegion(state) {
      if (state.activeRegion === "false") {
        return false;
      }
      else if (state.activeRegion === "null") {
        return null;
      }
      else {
        return state.activeRegion;
      }
    },
    activeCity(state) {
      if (state.activeCity === "false") {
        return false;
      }
      else if (state.activeCity === "null") {
        return null;
      }
      else {
        return state.activeCity;
      }
    },
    codes(state) {
      try {
        return JSON.parse(state.codes);
      } catch (e) {
        return state.codes;
      }
    },
    activeCode(state) {
      if (!state.activeCode) {
        return null;
      }
      else if (state.activeCode === "null") {
        return null
      }
      else {
        return state.activeCode;
      }
    },
    foreign(state) {
      if (state.foreign === "false") {
        return false;
      }
      else if (state.foreign === true) {
        return ""; // Default country (if not Ukraine)
      }
      else {
        return state.foreign
      }
    },
    foreignBool(state) {
      return state.foreign !== false;
    },
    countries(state) {
      try {
        return JSON.parse(state.countries);
      } catch (e) {
        return state.countries;
      }
    }
  },
  mutations: {
    changeRegions(state, value) {
      state.regions = value;
      localStorage.setItem("transportDataRegions", JSON.stringify(value))
    },
    changePlaceType(state, value) {
      state.placeType = value;
      localStorage.setItem("transportDataPlaceType", value);
    },
    changeRegion(state, value) {
      state.activeRegion = value;
      localStorage.setItem("transportDataRegion", value);
    },
    changeCity(state, value) {
      state.activeCity = value;
      localStorage.setItem("transportDataCity", value);
    },
    changeCodes(state, value) {
      state.codes = value;
      localStorage.setItem("transportDataCodes", JSON.stringify(value))
    },
    changeCode(state, value) {
      state.activeCode = value;
      if (value === null) {
        localStorage.setItem("transportDataCode", null);
      }
      else {
        localStorage.setItem("transportDataCodeNum", value.codeNum);
        localStorage.setItem("transportDataCodeReg", value.regionName);
        localStorage.setItem("transportDataCodeCity", value.cityName);
      }

    },
    changeForeign(state, value) {
      if (value === null) {
        value = "empty"
      }
      state.foreign = value;
      localStorage.setItem("transportDataForeign", value);
    },
    changeCounties(state, value) {
      state.countries = value;
      localStorage.setItem("transportDataCountries", JSON.stringify(value))
    }
  },
  actions: {
    changeRegions(store, value) {
      store.commit("changeRegions", value);
    },
    changePlaceType(store, value) {
      store.commit("changePlaceType", value);
    },
    changeRegion(store, value) {
      store.commit("changeRegion", value);
    },
    changeCity(store, value) {
      store.commit("changeCity", value);
    },
    changeCodes(store, value) {
      store.commit("changeCodes", value);
    },
    changeCode(store, value) {
      store.commit("changeCode", value);
    },
    changeForeign(store, value) {
      store.commit("changeForeign", value);
    },
    changeCountries(store, value) {
      store.commit("changeCounties", value);
    }
  }
};



