import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

import transport from "./modules/transportData/transport";
import place from "./modules/transportData/place";
import user from "./modules/transportData/user";
import period from "./modules/transportData/period";
import taxi from "./modules/transportData/taxi";
import dtp from "./modules/transportData/dtp";
import privileges from "./modules/transportData/privileges";
import park from "./modules/transportData/park";
import osago from "./modules/transportData/osago";
import otk from "./modules/transportData/otk";
import errors from "./modules/transportData/errors.js"
import selectedConditions from "./modules/companiesList/selectedConditions";
import term from "./modules/clientData/term";
import personalData from "./modules/clientData/personalData";
import documents from "./modules/clientData/documents";
import address from "./modules/clientData/address";
import vehicle from "./modules/clientData/vehicle";

export const store = new Vuex.Store({
  modules: {
    transport,
    place,
    user,
    period,
    otk,
    taxi,
    dtp,
    privileges,
    park,
    osago,
    errors,
    selectedConditions,
    term,
    personalData,
    documents,
    address,
    vehicle
  },
  getters: {
    transportData() {
      let placeObj = {};
      if (place.getters.placeType(place.state) === "address") {
        placeObj = {
          placeType: place.getters.placeType(place.state),
          region: place.getters.activeRegion(place.state),
          city: place.getters.activeCity(place.state),
          foreign: place.getters.foreign(place.state)
        }
      }
      else {
        placeObj = {
          placeType: place.getters.placeType(place.state),
          code: place.getters.activeCode(place.state)
        }
      }

      let dtpObj = {};
      if (!dtp.getters.dtpBool(dtp.state)) {
        dtpObj = {
          dtpState: dtp.getters.dtpBool(dtp.state)
        }
      }
      else {
        dtpObj = {
          dtpState: dtp.getters.dtpBool(dtp.state),
          dtpTerm: dtp.getters.dtp(dtp.state),
        }
      }

      let parkObj = {};
      if (!park.getters.parkBool(park.state)) {
        parkObj = {
          parkState: park.getters.parkBool(park.state)
        }
      }
      else {
        parkObj = {
          parkState: park.getters.parkBool(park.state),
          parkTerm: park.getters.park(park.state),
        }
      }

      return {
        trans: {
          type: transport.getters.transType(transport.state),
          extra: transport.getters.transExtraName(transport.state)
        },
        place: placeObj,
        isLegalEntity: user.getters.isLegalEntity(user.state),
        period: {
          term: period.getters.period(period.state),
          otk: otk.getters.otk(otk.state)
        },
        taxi: taxi.getters.taxi(taxi.state),
        dtp: dtpObj,
        privilege: privileges.getters.privilegesBool(privileges.state),
        park: parkObj,
        osagoLimits: osago.getters.osago(osago.state)
      };
    },

    selectedConditions() {
      return selectedConditions.getters.selectedConditions(selectedConditions.state)
    },

    clientData() {
      let placeObj = {};
      let personalDataObj = {};
      let documentsObj = {};

      if (documents.getters.type(documents.state) === "id-card") {
        documentsObj = {
          type: documents.getters.type(documents.state),
          number: documents.getters.number(documents.state),
          when: documents.getters.when(documents.state)
        }
      }
      else {
        documentsObj = {
          type: documents.getters.type(documents.state),
          lot: documents.getters.lot(documents.state),
          number: documents.getters.number(documents.state),
          issuedBy: documents.getters.issuedBy(documents.state),
          when: documents.getters.when(documents.state)
        }
      }
      if (user.getters.isLegalEntity(user.state) === false) {
        personalDataObj = {
          isLegalEntity: user.getters.isLegalEntity(user.state),
          inn: personalData.getters.inn(personalData.state),
          surname: personalData.getters.surname(personalData.state),
          name: personalData.getters.name(personalData.state),
          patronymic: personalData.getters.patronymic(personalData.state),
          dateOfBirth: personalData.getters.dateOfBirth(personalData.state),
          phone: personalData.getters.phone(personalData.state)
        }
      }
      else if (user.getters.isLegalEntity(user.state) === true) {
        personalDataObj = {
          isLegalEntity: user.getters.isLegalEntity(user.state),
          egrpou: personalData.getters.egrpou(personalData.state),
          companyName: personalData.getters.companyType(personalData.state) + '"' + personalData.getters.companyName(personalData.state) + '"',
          phone: personalData.getters.phone(personalData.state)
        }
      }
      if (place.getters.placeType(place.state) === "address") {
        placeObj = {
          placeType: place.getters.placeType(place.state),
          region: place.getters.activeRegion(place.state),
          city: place.getters.activeCity(place.state),
          foreign: place.getters.foreign(place.state)
        }
      }
      else {
        placeObj = {
          placeType: place.getters.placeType(place.state),
          code: place.getters.activeCode(place.state)
        }
      }

      return {
        termFrom: term.getters.termFrom(term.state),
        termTo: term.getters.termTo(term.state),
        frans: selectedConditions.getters.selectedConditions(selectedConditions.state).frans,
        company: selectedConditions.getters.selectedConditions(selectedConditions.state).company,
        personalData: personalDataObj,
        documents: documentsObj,
        region: address.getters.region(address.state),
        city: address.getters.city(address.state),
        street: address.getters.streetType(address.state) + " " + address.getters.street(address.state),
        house: address.getters.house(address.state),
        flat: address.getters.flat(address.state),
        placeReg: placeObj,
        taxi: taxi.getters.taxi(taxi.state),
        otk: otk.getters.otk(otk.state),
        transExtra: transport.getters.transExtraName(transport.state),
        autoNumber: vehicle.getters.autoNumber(vehicle.state),
        mark: vehicle.getters.mark(vehicle.state),
        model: vehicle.getters.model(vehicle.state),
        chassis: vehicle.getters.chassis(vehicle.state),
        autoYear: vehicle.getters.autoYear(vehicle.state),
        coefs: selectedConditions.getters.selectedConditions(selectedConditions.state).coefs,
        idCompanyMiddleman: selectedConditions.getters.selectedConditions(selectedConditions.state).idCompanyMiddleman
      }
    }
  }
});



