export default {
	namespaced: true,
	state: {
		taxi: localStorage.getItem("transportDataTaxi") === "true" || false
	},
	getters: {
		taxi(state) {
			return state.taxi;
		}
	},
	mutations: {
		changeTaxiValue(state, value) {
			state.taxi = value;
			localStorage.setItem("transportDataTaxi", value);
		}
	},
	actions: {
		changeTaxiValue(store, value) {
			store.commit("changeTaxiValue", value);
		}
	}
};



