<template>
  <div class="loading">
    <div class="spinner">
      <svg class="loading--svg" viewBox="0 0 66 66" xmlns="http://www.w3.org/2000/svg">
        <circle class="length" fill="none" stroke-width="8" stroke-linecap="round" cx="33" cy="33" r="28"></circle>
      </svg>
      <svg class="loading--svg" viewBox="0 0 66 66" xmlns="http://www.w3.org/2000/svg">
        <circle fill="none" stroke-width="8" stroke-linecap="round" cx="33" cy="33" r="28"></circle>
      </svg>
      <svg class="loading--svg" viewBox="0 0 66 66" xmlns="http://www.w3.org/2000/svg">
        <circle fill="none" stroke-width="8" stroke-linecap="round" cx="33" cy="33" r="28"></circle>
      </svg>
      <svg class="loading--svg" viewBox="0 0 66 66" xmlns="http://www.w3.org/2000/svg">
        <circle fill="none" stroke-width="8" stroke-linecap="round" cx="33" cy="33" r="28"></circle>
      </svg>
    </div>
    <p class="loading--text">{{message || messages[index] }}</p>
  </div>
</template>

<script>
  export default {
    props: ["message", "messages"],
    data() {
      return {
        index: 0
      }
    },

    computed: {},

    methods: {},

    mounted() {
      setInterval(
        _ => {
          if (this.index < this.messages.length - 1) {
            this.index++;
          }
          else {
            this.index = 0;
          }
        },
        3000
      )
    }
  }
</script>

<style>
  .loading {
    margin: 40px 0;
  }

  .loading__disabled {
    display: none;
  }

  .loading__disabled .spinner {
    animation: none;
  }

  .spinner {
    margin: 0 auto;
    width: 66px;
    height: 66px;
    animation: contanim 2s linear infinite;
  }

  .loading--svg {
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    position: absolute;
    transform: rotate(-90deg);
  }

  .loading--svg:nth-child(1) circle {
    stroke: #84EBBD;
    stroke-dasharray: 1, 300;
    stroke-dashoffset: 0;
    animation: strokeanim 3s calc(.2s * (1)) ease infinite;
    transform-origin: center center;
  }

  .loading--svg:nth-child(2) circle {
    stroke: #333841;
    stroke-dasharray: 1, 300;
    stroke-dashoffset: 0;
    animation: strokeanim 3s calc(.2s * (2)) ease infinite;
    transform-origin: center center;
  }

  .loading--svg:nth-child(3) circle {
    stroke: #F6BB67;
    stroke-dasharray: 1, 300;
    stroke-dashoffset: 0;
    animation: strokeanim 3s calc(.2s * (3)) ease infinite;
    transform-origin: center center;
  }

  .loading--svg:nth-child(4) circle {
    stroke: #0093e8;
    stroke-dasharray: 1, 300;
    stroke-dashoffset: 0;
    animation: strokeanim 3s calc(.2s * (4)) ease infinite;
    transform-origin: center center;
  }

  @keyframes strokeanim {
    0% {
      stroke-dasharray: 1, 300;
      stroke-dashoffset: 0;
    }
    50% {
      stroke-dasharray: 120, 300;
      stroke-dashoffset: -58.54832;
    }
    100% {
      stroke-dasharray: 120, 300;
      stroke-dashoffset: -175.64497;
    }
  }

  @keyframes contanim {
    100% {
      transform: rotate(360deg);
    }
  }

  .loading--text {
    margin-top: 20px;
    color: #0093e8;
  }
</style>



