export default {
	namespaced: true,
	state: {
		mark: "",
		model: "",
		chassis: "",
		autoNumber: "",
		autoYear: ""
	},
	getters: {
		mark(state) {
			return state.mark;
		},
		model(state) {
			return state.model;
		},
		chassis(state) {
			return state.chassis;
		},
		autoNumber(state) {
			return state.autoNumber;
		},
		autoYear(state) {
			return state.autoYear;
		}
	},
	mutations: {
		changeMarkValue(state, value) {
			state.mark = value;
		},
		changeModelValue(state, value) {
			state.model = value;
		},
		changeChassisValue(state, value) {
			state.chassis = value;
		},
		changeAutoNumberValue(state, value) {
			state.autoNumber = value;
		},
		changeAutoYearValue(state, value) {
			state.autoYear = value;
		}
	},
	actions: {
		changeMarkValue(store, value) {
			store.commit("changeMarkValue", value);
		},
		changeModelValue(store, value) {
			store.commit("changeModelValue", value);
		},
		changeChassisValue(store, value) {
			store.commit("changeChassisValue", value);
		},
		changeAutoNumberValue(store, value) {
			store.commit("changeAutoNumberValue", value);
		},
		changeAutoYearValue(store, value) {
			store.commit("changeAutoYearValue", value);
		}
	}
};



