import place from "./place";
import period from "./period";

export default {
	namespaced: true,
	state: {
	},
	getters: {
		errors(state) {
			return {
				region: place.getters.placeType(place.state) === "address" && place.getters.activeRegion(place.state) === null && !place.getters.foreign(place.state),
				city: place.getters.placeType(place.state) === "address" && place.getters.activeCity(place.state) === null && !place.getters.foreign(place.state),
				code: place.getters.placeType(place.state) === "code" && place.getters.activeCode(place.state) === null,
				country: place.getters.foreign(place.state) === "empty" || place.getters.foreign(place.state) === "",
				period: !period.getters.period(period.state)
      }
		},
		errorsLength(state, getters) {
			let length = 0;
			for (let key in getters.errors) {
				if (getters.errors[key] === true) {
					length++;
				}
			}
			return length;
		}
	}
};



