export default {
	namespaced: true,
	state: {
		park: (localStorage.getItem("transportDataPark") && localStorage.getItem("transportDataPark") !== "false") ?
      localStorage.getItem("transportDataPark")
      :
      false
	},
	getters: {
    park(state) {
		  if (state.park) {
        return +state.park;
      }
			else {
        return state.park;
      }
		},
    parkBool(state) {
      return state.park !== false
    }
	},
	mutations: {
		changeParkValue(state, value) {
			state.park = value;
			localStorage.setItem("transportDataPark", value);
		}
	},
	actions: {
    changeParkValue(store, value) {
			store.commit("changeParkValue", value);
		}
	}
};



