export default {
	namespaced: true,
	state: {
		osago: +localStorage.getItem("transportDataOsago") || 0
	},
	getters: {
		osago(state) {
			return state.osago;
		}
	},
	mutations: {
		changeOsagoValue(state, value) {
			state.osago = +value;
			localStorage.setItem("transportDataOsago", value);
		}
	},
	actions: {
		changeOsagoValue(store, value) {
			store.commit("changeOsagoValue", value);
		}
	}
};



