export default {
	namespaced: true,
	state: {
		dtp: (localStorage.getItem("transportDataDtp") && localStorage.getItem("transportDataDtp") !== "false") ?
      localStorage.getItem("transportDataDtp")
      :
      false
	},
	getters: {
		dtp(state) {
		  if (state.dtp) {
        return +state.dtp;
      }
			else {
        return state.dtp;
      }
		},
    dtpBool(state) {
      return state.dtp !== false
    }
	},
	mutations: {
		changeDtpValue(state, value) {
			state.dtp = value;
			localStorage.setItem("transportDataDtp", value);
		}
	},
	actions: {
		changeDtpValue(store, value) {
			store.commit("changeDtpValue", value);
		}
	}
};



