export default {
	namespaced: true,
	state: {
		termFrom: new Date().toISOString().split("T")[0],
    termTo: "",
	},
	getters: {
    termFrom(state) {
			return state.termFrom;
		},
    termTo(state) {
			return state.termTo;
		}
	},
	mutations: {
		changeTermFrom(state, value) {
			state.termFrom = value;
		},
    changeTermTo(state, value) {
      state.termTo = value;
    }
	},
	actions: {
    changeTermFrom(store, value) {
			store.commit("changeTermFrom", value);
		},
    changeTermTo(store, value) {
			store.commit("changeTermTo", value);
		}
	}
};



